* {
  font-family: Nunito Sans;
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: black;
}

::-webkit-scrollbar {
  width: 12px;
  height: 0px;
  cursor: pointer;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(143, 143, 143);
}

::-webkit-scrollbar-track {
  background-color: white;
}

.App {
  padding: 0vh 10vw;
  min-height: 100vh;
  background-color: rgb(247, 247, 247);
  // padding-bottom: 2cm;
  scroll-behavior: smooth;
}

// Home Page Starts Here

.home-box {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 90vh;
  // border: 1px solid black;
}

.content-home-box {
  // border: 1px solid black;
  width: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: .5cm;
}

.home-name {

  font-size: 100px;
  font-weight: bold;
  font-family: Product Sans Regular;
  letter-spacing: .1cm;


}

.n {
  color: #4385f5;
}

.i {
  color: #eb4235;
}

.t {
  color: #fabd05;
}

.e {
  color: #4385f5;

  transform: rotate(-15deg);
  /* Adjust the angle as needed */
  display: inline-block;
  /* Ensures the rotation doesn't affect the layout */
}

.s {
  color: #34a853;
}

.h {
  color: #eb4235;
}

.welcome-box {

  padding: .2cm .3cm;
  display: flex;
  align-items: center;
  margin-top: -.5cm;
  gap: .2cm;
  width: 12cm;
  border: 1px solid rgb(179, 179, 179);
  border-radius: 20px;

}

.welcome {
  font-size: 16px;
  border: none;
  background-color: transparent;
}


.wave {
  width: .7cm;
  height: auto;
  transform: rotate(10deg);

}

.getStarted {
  padding: .2cm .5cm;
  font-size: 16px;
  border-radius: 20px;
  border: 1px solid rgb(196, 196, 196);
  background-color: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;

  transition: .3s;
  cursor: pointer;

  &:hover {
    background-color: rgb(214, 255, 214);
   
  }
}

input:focus {
  outline: transparent;
  border: 1px solid rgb(179, 179, 179);

}

.welcome:focus {
  outline: none;
  border: none;
}




// Home Page Ends Here


// Navigation Bar Starts Here

.nav-box {
  padding: .5cm 0cm;

}

.menu {
  display: flex;
  align-items: center;
  gap: .5cm;
}

.nav-name {
  font-size: 40px;
  letter-spacing: 0.04cm;
}

.nav-inp {
  width: 17cm;
  padding: .2cm .3cm;
  border: 1px solid rgb(179, 179, 179);
  background-color: transparent;
  border-radius: 20px;
  font-size: 16px;
}

.menu-list {
  display: flex;
  align-items: center;
  gap: .3cm;
  margin-left: 4cm;
}

.nav-box {
  display: flex;
  flex-direction: column;
  gap: .2cm;
}

.menuItem {
  font-weight: 600;
  font-size: 16px;
  border-bottom: 2.5px solid transparent;
  transition: .3s all ease;
  cursor: pointer;

  &:hover {
    border-bottom: 2.5px solid royalblue;
  }
}

.activeItem {
  border-bottom: 2.5px solid royalblue;
}


.Portfolio {
  display: flex;
  align-items: flex-start;
  gap: .5cm;
  margin-top: .5cm;
  padding-bottom: 2cm;
  // padding: 0cm .3cm;
  // border: 1px solid black;

}



.portfolio-box {
  // border: 1px solid black;
  width: 17.3cm;
  margin-left: 4cm;
  // margin-top: .5cm;
  display: flex;
  flex-direction: column;
  gap: .5cm;
}

.link-head {
  font-size: 20px;
  font-weight: 600;
  color: rgb(26, 79, 238);
  width: fit-content;
  transition: .3s all ease;
  cursor: pointer;
  border-bottom: 2px solid transparent;

  &:hover {
    border-bottom: 2px solid rgb(26, 79, 238);
  }
}

.link-dir {
  font-size: 16px;
  color: rgb(7, 117, 7);
}

.link-dets {
  font-size: 15px;
  color: rgb(87, 87, 87);
}

.contact-box-portfolio {
  margin-top: .5cm;
  background-color: white;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  // border: 1px solid black;
  padding: .2cm .3cm .3cm .3cm;
  border-radius: 8px;
}

.ch {
  color: black;
  cursor: default;


  &:hover {
    border-bottom: 2px solid transparent;
  }
}

.contact-me {
  border: none;
  padding: .2cm .6cm;
  margin-top: .5cm;
  background-color: rgb(26, 79, 238);
  color: white;
  font-size: 16px;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  transition: all .3s ease;

  &:hover {
    background-color: rgb(30, 71, 194);
  }
}

.image-box {
  // border: 1px solid black;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
  border-radius: 10px;
  background-color: white;
  width: 100%;
  flex: 1;
}

.ppic {
  width: 100%;
  height: auto;
  border-radius: 10px 10px 0px 0px;
}

.dets-box {
  padding: 0.2cm .4cm .2cm .4cm;
}

.name-det {
  font-size: 25px;
}

.tagline {
  // font-size: 15px;
  color: rgb(102, 102, 102);
}

.name-box-dets {
  border-bottom: 1px solid rgb(219, 219, 219);
  padding-bottom: .1cm;
}

.rest-dets-box {
  margin-top: .2cm;
  display: flex;
  flex-direction: column;
  gap: .1cm;
  border-bottom: 1px solid rgb(219, 219, 219);
  padding-bottom: .1cm;
}

.dets-head {
  font-weight: 600;
}

.dets-text {
  // font-size: 15px;
  // color: rgb(26, 26, 26);
}

.important-links {
  margin-top: .2cm;
}

.il-head {
  font-size: 20px;
}

.links-box {
  display: flex;
  flex-direction: column;
  gap: .1cm;
}

.link-box-link {
  // font-size: 18px;
  color: rgb(26, 79, 238);
  cursor: pointer;
  transition: .2s all;
  border-bottom: 1px solid rgb(219, 219, 219);

  &:hover {
    color: rgb(30, 71, 194);
  }
}

.contact-page {
  font-size: 18px;
  color: black;
}


.contact-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: .5cm;
  flex-wrap: wrap;
  margin-top: .5cm;
}

.cb {
  padding: .3cm 1.2cm;
  background-color: royalblue;
  color: white;
  font-size: 20px;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;

}

button:active {
  box-shadow: none;
}

.cbox {
  box-shadow: none;
  background-color: transparent;
  border-radius: 0px;
}

.cpic {
  border-radius: 0px;
}